import { NextRequest, NextResponse } from 'next/server';
import { readUsers, writeUsers } from '@/lib/db';
import { generateToken, verifyToken } from '@/lib/auth';

export async function POST(request: NextRequest) {
  try {
    const { username, password } = await request.json();

    if (!username || !password) {
      return NextResponse.json(
        { error: 'Username and password are required' },
        { status: 400 }
      );
    }

    const users = await readUsers();
    const user = users.find(u => u.username === username && u.password === password);

    if (!user) {
      return NextResponse.json(
        { error: 'Credenciais inválidas' },
        { status: 401 }
      );
    }

    // Check if user is already active (single device restriction)
    if (user.active) {
      // Check if this login is from the same user with valid token
      const authHeader = request.headers.get('authorization');
      if (authHeader && authHeader.startsWith('Bearer ')) {
        const token = authHeader.substring(7);
        const payload = verifyToken(token);
        if (payload && payload.userId === user.id) {
          // Same user and valid token, allow login
        } else {
          return NextResponse.json(
            { error: 'Este usuário já está ativo em outro dispositivo' },
            { status: 403 }
          );
        }
      } else {
        return NextResponse.json(
          { error: 'Este usuário já está ativo em outro dispositivo' },
          { status: 403 }
        );
      }
    }

    // Set user as active
    const updatedUsers = users.map(u => 
      u.id === user.id ? { ...u, active: true } : u
    );
    
    await writeUsers(updatedUsers);

    // Generate JWT token
    const token = generateToken({
      userId: user.id,
      username: user.username,
      role: user.role
    });

    // Return user data without password
    const { password: _, ...userWithoutPassword } = user;

    return NextResponse.json({
      success: true,
      token,
      user: { ...userWithoutPassword, active: true }
    });

  } catch (error) {
    console.error('Login error:', error);
    return NextResponse.json(
      { error: 'Erro interno do servidor' },
      { status: 500 }
    );
  }
}

export async function DELETE(request: NextRequest) {
  try {
    const authHeader = request.headers.get('authorization');
    if (!authHeader || !authHeader.startsWith('Bearer ')) {
      return NextResponse.json(
        { error: 'Token de autorização necessário' },
        { status: 401 }
      );
    }

    const token = authHeader.substring(7);
    const payload = verifyToken(token);

    if (!payload) {
      return NextResponse.json(
        { error: 'Token inválido' },
        { status: 401 }
      );
    }

    // Set user as inactive (logout)
    const users = await readUsers();
    const updatedUsers = users.map(u => 
      u.id === payload.userId ? { ...u, active: false } : u
    );
    
    await writeUsers(updatedUsers);

    return NextResponse.json({ success: true, message: 'Logout realizado com sucesso' });

  } catch (error) {
    console.error('Logout error:', error);
    return NextResponse.json(
      { error: 'Erro interno do servidor' },
      { status: 500 }
    );
  }
}
