import { promises as fs } from 'fs';
import path from 'path';

export interface User {
  id: number;
  username: string;
  password: string;
  role: 'admin' | 'user';
  active: boolean;
  questionsAnswered: number;
  correctAnswers: number;
  createdAt: string;
}

export interface Question {
  id: number;
  subject: string;
  question: string;
  options: string[];
  correctAnswer: number;
  createdAt: string;
}

const DATA_DIR = path.join(process.cwd(), 'data');
const USERS_FILE = path.join(DATA_DIR, 'users.json');
const QUESTIONS_FILE = path.join(DATA_DIR, 'questions.json');

export async function readUsers(): Promise<User[]> {
  try {
    const data = await fs.readFile(USERS_FILE, 'utf8');
    return JSON.parse(data);
  } catch (error) {
    console.error('Error reading users file:', error);
    return [];
  }
}

export async function writeUsers(users: User[]): Promise<boolean> {
  try {
    await fs.writeFile(USERS_FILE, JSON.stringify(users, null, 2));
    return true;
  } catch (error) {
    console.error('Error writing users file:', error);
    return false;
  }
}

export async function readQuestions(): Promise<Question[]> {
  try {
    const data = await fs.readFile(QUESTIONS_FILE, 'utf8');
    return JSON.parse(data);
  } catch (error) {
    console.error('Error reading questions file:', error);
    return [];
  }
}

export async function writeQuestions(questions: Question[]): Promise<boolean> {
  try {
    await fs.writeFile(QUESTIONS_FILE, JSON.stringify(questions, null, 2));
    return true;
  } catch (error) {
    console.error('Error writing questions file:', error);
    return false;
  }
}

export async function getUserById(id: number): Promise<User | null> {
  const users = await readUsers();
  return users.find(user => user.id === id) || null;
}

export async function getUserByUsername(username: string): Promise<User | null> {
  const users = await readUsers();
  return users.find(user => user.username === username) || null;
}

export async function updateUser(updatedUser: User): Promise<boolean> {
  const users = await readUsers();
  const index = users.findIndex(user => user.id === updatedUser.id);
  
  if (index === -1) {
    return false;
  }
  
  users[index] = updatedUser;
  return await writeUsers(users);
}

export async function addUser(newUser: Omit<User, 'id'>): Promise<User | null> {
  const users = await readUsers();
  const maxId = users.reduce((max, user) => Math.max(max, user.id), 0);
  
  const userWithId: User = {
    ...newUser,
    id: maxId + 1,
  };
  
  users.push(userWithId);
  const success = await writeUsers(users);
  
  return success ? userWithId : null;
}

export async function addQuestion(newQuestion: Omit<Question, 'id'>): Promise<Question | null> {
  const questions = await readQuestions();
  const maxId = questions.reduce((max, question) => Math.max(max, question.id), 0);
  
  const questionWithId: Question = {
    ...newQuestion,
    id: maxId + 1,
  };
  
  questions.push(questionWithId);
  const success = await writeQuestions(questions);
  
  return success ? questionWithId : null;
}

export async function getQuestionsBySubject(subject: string): Promise<Question[]> {
  const questions = await readQuestions();
  return questions.filter(question => question.subject === subject);
}

export async function getAllSubjects(): Promise<string[]> {
  const questions = await readQuestions();
  const subjects = [...new Set(questions.map(question => question.subject))];
  return subjects.sort();
}
